import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { trigger, style, transition, animate, keyframes, query, stagger } from '@angular/animations';
import { DataService } from '../dataService';

@Component({
    selector: 'app-turma3',
    templateUrl: './turma3.component.html',
    styleUrls: ['./turma3.component.scss'],
    animations: [

        trigger('linhas', [
            transition('* => *', [

                query(':enter', style({ opacity: 0 }), { optional: true }),

                query(':enter', stagger('300ms', [
                    animate('.6s ease-in', keyframes([
                        style({ opacity: 0, transform: 'translateY(-75%)', offset: 0 }),
                        style({ opacity: 0.5, transform: 'translateY(35px)', offset: .3 }),
                        style({ opacity: 1, transform: 'translateY(0)', offset: 1 }),
                    ]))
                ]), { optional: true }),

                query(':leave', stagger('300ms', [
                    animate('.6s ease-in', keyframes([
                        style({ opacity: 1, transform: 'translateY(0)', offset: 0 }),
                        style({ opacity: 0.5, transform: 'translateY(35px)', offset: .3 }),
                        style({ opacity: 0, transform: 'translateY(-75%)', offset: 1 }),
                    ]))
                ]), { optional: true })
            ])
        ])

    ]
})
export class Turma3Component implements OnInit {

    public alunos: Array<any>;
    public labels = [];
    public provas1: Array<any>;
    public provas2: Array<any>;
    public provas3: Array<any>;
    public medias = [];
    public provasf = [];
    public mediasf = [];
    public situacao = [];
    public indices = [];
    public check = [];

    constructor(private data: DataService) {
    }

    ngOnInit() {

        this.data.carregarAlunos()
            .subscribe(success => {
                if (success) {
                    this.alunos = this.data.alunos.filter(aluno => aluno.turma == 3);

                }
            });

        this.data.carregarProvas()
            .subscribe(success => {

                this.provas1 = this.data.provas.filter(prova => prova.tipo == 1);
                this.provas1.splice(0, 40);
                this.provas2 = this.data.provas.filter(prova => prova.tipo == 2);
                this.provas2.splice(0, 40);
                this.provas3 = this.data.provas.filter(prova => prova.tipo == 3);
                this.provas3.splice(0, 40);
                this.provasf = this.data.provas.filter(prova => prova.tipo == 4);
                this.provasf.splice(0, 40);
                

                for (var i = 0; i < 20; i++) {
                    this.medias[i] = (this.provas1[i].nota + this.provas2[i].nota * 1.2 + this.provas3[i].nota * 1.4) / 3.6;
                    if (this.medias[i] < 4) {
                        this.provasf[i].nota = 0;
                        this.mediasf[i] = this.medias[i];
                        this.situacao[i] = "Reprovado";
                    } else if (this.medias[i] > 6) {
                        this.provasf[i].nota = 0;
                        this.mediasf[i] = this.medias[i];
                        this.situacao[i] = "Aprovado";
                    } else {
                        this.mediasf[i] = ((this.medias[i] + this.provasf[i].nota) / 2);
                        this.provasf[i].nota = this.provasf[i].nota.toFixed(2);
                        if (this.mediasf[i] > 5) {
                            this.situacao[i] = "Aprovado";
                        } else {
                            this.situacao[i] = "Reprovado";
                        }
                    }
                }



            });

        this.labels = ["Aluno", "Turma", "Prova 1", "Peso 1", "Prova 2", "Peso 2", "Prova 3", "Peso 3","Media", "Prova Final", "Media Final", "Situacao"];


    }



}
