﻿import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import { BehaviorSubject } from 'rxjs';
import { map } from 'rxjs/operators';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';


@Injectable()
export class DataService {
    
    constructor(private http: HttpClient) {
    }

    
    public alunos: Array<any>;
    public provas: Array<any>;
    public provas1: Array<any>;
    public provas2: Array<any>;
    public provas3: Array<any>;
    public finalistas: Array<any>;
    public mediasf = [];
    public medias = [];
    public situacao = [];

    
    carregarAlunos() {
        return this.http.get("api/aluno").pipe(
            map((dataA: any[]) => {
                this.alunos = dataA;
                return true;
            })); 
           
    }

    carregarProvas() {
        return this.http.get("api/prova").pipe(
            map((dataP: any[]) => {
                this.provas = dataP;

                return true;
            }));
    }


}