import { Component, OnInit } from '@angular/core';
import { Observable, PartialObserver } from 'rxjs';
import { trigger, style, transition, animate, keyframes, query, stagger } from '@angular/animations';
import { DataService } from '../dataService';

import { BehaviorSubject } from 'rxjs';

@Component({
  selector: 'app-compet',
  templateUrl: './compet.component.html',
  styleUrls: ['./compet.component.scss'],
  animations: [

    trigger('linhas', [
      transition('* => *', [
  
        query(':enter', style({opacity:0}), {optional:true}),
  
        query(':enter',stagger('300ms', [
          animate('.6s ease-in',keyframes([
            style({opacity: 0, transform:'translateY(-75%)',offset:0}),
            style({opacity: 0.5, transform:'translateY(35px)',offset:.3}),
            style({opacity: 1, transform:'translateY(0)',offset:1}),
          ]))
        ]), {optional:true}),
  
        query(':leave',stagger('300ms', [
          animate('.6s ease-in',keyframes([
            style({opacity: 1, transform:'translateY(0)',offset:0}),
            style({opacity: 0.5, transform:'translateY(35px)',offset:.3}),
            style({opacity: 0, transform:'translateY(-75%)',offset:1}),
          ]))
        ]), {optional:true})
      ])
    ])
  
      ]
})
export class CompetComponent implements OnInit {

    constructor(private data: DataService) {
    }
    
    

    public alunos: Array<any>;
    public alunosf = [];
    public provas1 = [];
    public provas2 = [];
    public provas3 = [];
    public provasC = [];
    public medias = [];
    public mediasf = [];
    public indices = [];
    public situacao = [];
    public labels = [];
    public temp = [];

    ngOnInit(): void {

        this.data.carregarAlunos()
            .subscribe(success => {
                if (success) {
                    this.alunos = this.data.alunos;
                    
                }
            })

        this.data.carregarProvas()
            .subscribe(success => {
                if (success) {

                    this.provas1 = this.data.provas.filter(prova => prova.tipo == 1);
                    this.provas2 = this.data.provas.filter(prova => prova.tipo == 2);
                    this.provas3 = this.data.provas.filter(prova => prova.tipo == 3);
                    
                    //INICIALIZA VALORES ALEATÓRIOS PARA A PROVA ESPECIAL
                    for (var j = 0; j < 5; j++) {
                        this.provasC[j] = Math.random() * 10;
                    }

                    //CALCULA AS MÉDIAS DOS ALUNOS
                    for (var i = 0; i < this.provas1.length; i++) {
                        this.medias[i] = (this.provas1[i].nota + this.provas2[i].nota * 1.2 + this.provas3[i].nota * 1.4) / 3.6;
                        this.mediasf[i] = (this.provas1[i].nota + this.provas2[i].nota + this.provas3[i].nota + this.provasC[i] * 2) / 5;
                    }
                    
                    //BUSCA OS ÍNDICES DAS 5 MAIORES MÉDIAS
                    Loop1:
                    for (var i = 0; i < this.mediasf.length; i++) {
                        if (this.indices.length < 5){
                            this.indices[i] = i;
                        }
                        else {
                            Loop2:
                            for (var j = 0; j < 5; j++) {
                                if (this.mediasf[i] > this.mediasf[this.indices[j]]) {
                                    this.indices[j] = i;
                                    break Loop2;
                                }
                            }
                        }
                    }
                    
                    
                    
                    for (var i = 0; i < this.indices.length; i++) {

                        this.temp[i] = this.mediasf[this.indices[i]];
                        this.alunosf[i] = this.alunos[this.indices[i]];

                         this.situacao[i] = "Bom trabalho";
                    }

                    this.situacao[this.temp.reduce((iMax, x, i, arr) => x > arr[iMax] ? i : iMax, 0)] = "Vencedor";
                    //this.situacao[this.temp.indexOf(Math.max(...this.temp))] = "Vencedor";
                    
                }
            })
        
        this.labels = ["Aluno", "Turma", "Prova 1", "Peso 1", "Prova 2", "Peso 2", "Prova 3", "Peso 3",
            "Prova Especial", "Peso Competição", "Média Final", "Situação"];
    }

}
